import { faEnvelope, faMapMarkerAlt, faPhone } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import axios from 'axios'
import { useState } from 'react'
import '../../Styles/Contact.css'

export default function ContactBody(props) {  
    
    const home = props.home
    const [contactEmail, setContactEmail] = useState("") 
    const [contactName, setContactName] = useState("") 
    const [contactContent, setContactContent] = useState("") 

    const onSubmit = (event) => {
        event.preventDefault()  

        axios.post('https://uel-law.herokuapp.com/contact', {
            contactEmail: contactEmail,
            contactName: contactName,
            contactContent: contactContent
        })
        .then(()=>{
            alert("Gi email thnh cng!")
            setContactEmail("")
            setContactContent("")
            setContactName("")
        })
    } 

    return (
        <div className="ContactBody">    
            <div className="about-header flex-center flex-col">
                <div className="about-title">Lin h</div>
                <div className="about-title-text">in thng tin  bn di  lin h vi chng ti</div>
                <div className="h-gap"></div>
            </div>
            <div className="contact-body flex">
                <div className="contact-address">
                    <p>Ngoi ra bn c th lin h bng nhng thng tin bn di</p>
                    <div className="contact-address-item flex">
                        <FontAwesomeIcon icon={faMapMarkerAlt} className="icon"/>
                        <p>{home.homeContactAddress}</p>
                    </div> 
                    <div className="contact-address-item flex">
                        <FontAwesomeIcon icon={faPhone} className="icon"/>
                        <p>{home.homeContactPhone}</p>
                    </div>
                    <div className="contact-address-item flex">
                        <FontAwesomeIcon icon={faEnvelope} className="icon"/>
                        <p>{home.homeContactEmail}</p>
                    </div>  
                </div>
                <form className="contact-form" onSubmit={onSubmit}> 
                    <input 
                        placeholder="H v tn..."
                        value={contactName}
                        onChange={(event)=>{
                            setContactName(event.target.value)
                        }}
                    ></input> 
                    <input placeholder="a ch Email..."
                        type="email"
                        value={contactEmail}
                        onChange={(event)=>{
                            setContactEmail(event.target.value)
                        }}
                    ></input> 
                    <textarea 
                        className="contact-content" 
                        placeholder="Ni dung tin nhn..." 
                        value={contactContent}
                        onChange={(event)=>{
                            setContactContent(event.target.value)
                        }} 
                    ></textarea>
                    <button className="contact-btn">Gi</button>
                </form>
            </div>
        </div>
    )
}